.App {
  font: 13px "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}

h1 {
  text-align: center;
}

h2 {
  margin: 5px 0 0 20px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.container {
  width: 60%;
  height: 450px;
  margin: auto;
  padding: 10px;
}

.add-new {
  width: 50%;
  float: left;
}

.search {
  margin-left: 50%;
}

.search input {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  border:1px solid #BEBEBE;
  padding: 7px;
  margin: 20px 0 0 30px;
  -webkit-transition: all 0.30s ease-in-out;
  -moz-transition: all 0.30s ease-in-out;
  -ms-transition: all 0.30s ease-in-out;
  -o-transition: all 0.30s ease-in-out;
  outline: none;
}

.search input:focus {
  -webkit-box-shadow: 0 0 8px #88D5E9;
  box-shadow: 0 0 8px #88D5E9;
  border: 1px solid #88D5E9;
}

.add-form {
  margin:0px auto;
  margin-left: 10px;
  padding: 20px 12px 10px 20px;
  font: 13px "Lucida Sans Unicode", "Lucida Grande", sans-serif;
}

.add-form li {
  padding: 0;
  display: block;
  list-style: none;
  margin: 10px 0 0 0;
}

.search li {
  padding: 0;
  display: block;
  list-style: none;
  margin: 10px 0 0 0;
}

.add-form label {
  margin: 0 0 3px 0;
  padding: 0px;
  display: block;
  font-weight: bold;
}

.add-form input {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  border:1px solid #BEBEBE;
  padding: 7px;
  margin: 0px;
  -webkit-transition: all 0.30s ease-in-out;
  -moz-transition: all 0.30s ease-in-out;
  -ms-transition: all 0.30s ease-in-out;
  -o-transition: all 0.30s ease-in-out;
  outline: none;
}

.add-form input:focus {
  -webkit-box-shadow: 0 0 8px #88D5E9;
  box-shadow: 0 0 8px #88D5E9;
  border: 1px solid #88D5E9;
}

.add-form .names {
  width: 120px;
  margin-right: 5px;
}

.required {
  color:red;
}

#zip-code {
  width: 120px;
  margin-right: 5px;
  border: 2px solid #777;
}

.add-form .location {
  width: 120px;
  margin: 5px 5px 0 0;
}

.add-form .lat-lng {
  width: 60px;
  margin: 5px 5px 0 0;
}

.add-form button {
  background: #4B99AD;
  padding: 8px 15px 8px 15px;
  border: 2px solid #777;
  color: #fff;
  font-weight: bold;
  margin-left: 50px;
}

.add-form button:hover {
  background: #4691A4;
  box-shadow:none;
  border: 2px solid #333;
  -moz-box-shadow:none;
  -webkit-box-shadow:none;
}

.contact-list {
  display: block;
  text-align: center;
  margin: auto;
  width: 80%;
}

.table {
  display: table;
  border-collapse: separate;
  border-spacing: 0px;
}

.thead {
  display: table-header-group;
  color: #FFF;
  font-weight: bold;
  background-color: #4899AD;
}

.tbody {
  display: table-row-group;
}

.tr {
  display: table-row;
}

.td {
  display: table-cell;
  border: 1px;
  border-color: #777;
  padding: 5px;
  border: 1px solid #777;
}

.table input {
  width: 100%;
}

.thead .td {
  padding: 2px 10px;
  text-align: center;
  border: 1px solid #777;
}

.thead .td:hover {
  background: #4691A4;
  box-shadow:none;
  border: 1px solid #333;
  -moz-box-shadow:none;
  -webkit-box-shadow:none;
  cursor: pointer;
}

.suggest {
  height: 350px;
  overflow: scroll;
  overflow-y: auto;
  overflow-x: hidden;
}

.instructions {
  font-size: 16px;
  font-weight: bold;
  text-align: center;
}


